.skeleton {
  &__header {
    display: grid;
    grid-template-columns: 40px auto;
    column-gap: 10px;
    align-items: center;
  }
  &__circle {
    width: 40px;
    height: 40px;
    background-color: #c4c4c4;
    border-radius: 100%;
  }
  &__mini {
    width: 100%;
    height: 16px;
    background-color: #c4c4c4;
  }
  &__block {
    height: 35px;
    width: 100%;
    background-color: #c4c4c4;
    margin-top: 15px;
  }
}

.pulse {
  animation: pulse 1.5s ease-in-out 0.5s infinite;
}

@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}